import React, { useEffect } from "react";
import { useSelector } from "react-redux";

export default function Documents(props) {
  const restaurant = useSelector((state) => state.restaurant);
  const { documents } = restaurant;
  useEffect(() => {
    console.log(documents);
  }, [documents]);
  const handleContinue = (e) => {
    props.goToStep(3);
  };
  const handleBack = (e) => {
    e.preventDefault();
    props.goToStep(1);
  };

  return (
    <fieldset>
      <div className="row mt-2">
        {documents &&
          documents.map((image, key) => (
            <div className="col-lg-3">
              <div className="image">
                <img
                  className="img-fluid"
                  alt="Profile"
                  src={Object.values(image)}
                />
              </div>
              <div className="file-name">{Object.keys(image)}</div>
            </div>
          ))}
      </div>
      <div className="row mt-2">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-primary float-right mr-2 "
            onClick={handleContinue}
          >
            Next
          </button>
          <button
            type="button"
            className="btn btn-default float-right mr-2 "
            onClick={handleBack}
          >
            Previous
          </button>
        </div>
      </div>
    </fieldset>
  );
}
