import React from "react"

export default function BankInfo(props) {

  const handleBack = (e) => {
    props.goToStep(4);
  };
  return (
    <fieldset>
      <div className="row mt-2">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              ACCOUNT NAME 
            </label>
            <input
              name="account_name"
              type="text"
              className="form-control required"
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              ACCOUNT NUMBER 
            </label>
            <input
              name="account_number"
              type="text"
              className="form-control required"
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              CONFIRM ACCOUNT NUMBER 
            </label>
            <input
              name="confirm_account_number"
              type="text"
              className="form-control required"
            />
          </div>
        </div>
      </div>
      <div className="row mt-2">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              BANK NAME 
            </label>
            <input
              name="bank_name"
              type="text"
              className="form-control required"
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              BRANCH # 
            </label>
            <input
              name="branch_number"
              type="text"
              className="form-control required"
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              INSTITUTION # 
            </label>
            <input
              name="institution_number"
              type="text"
              className="form-control required"

            />
          </div>
        </div>
      </div>

      <div className="row">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-default float-right mr-2 "
            onClick={handleBack}
          >
            Previous
          </button>
        </div>
      </div>
    </fieldset>
  );
}
