import React, { useState } from "react";

export default function Plans(props) {

  const handleContinue = (e) => {
    props.goToStep(5);
  };
  const handleBack = (e) => {
    props.goToStep(3);
  };

  return (
    <fieldset>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price  &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_2price"
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer2price"
            />
          </div>
        </div>
      </div>
      <p>
        <strong>15 days</strong>
      </p>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price  &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_15price"
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer15price"
            />
          </div>
        </div>
      </div>
      <p>
        <strong>30 Days</strong>
      </p>
      <div className="row">
        <div className="col-lg-4">
          <div className="form-group">
            <label>
              Base price  &nbsp;($)
            </label>
            <input
              className="form-control"
              type="currency"
              name="base_30price"
            />
          </div>
        </div>
        <div className="col-lg-4">
          <div className="form-group">
            <label>Customer price &nbsp;($)</label>
            <input
              className="form-control"
              type="currency"
              name="customer30price"
            />
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-lg-12 justify-content-end">
          <button
            type="button"
            className="btn btn-primary float-right mr-2 "
            onClick={handleContinue}
          >
            Next
          </button>
          <button
            type="button"
            className="btn btn-default float-right mr-2 "
            onClick={handleBack}
          >
            Previous
          </button>
        </div>
      </div>
    </fieldset>
  );
}
