import React, { useState } from "react";
import DynamicMeal from "./ViewDynamicMeal";
import { Tabs, Tab } from "react-bootstrap";


export default function Meals(props) {
  const [key, setKey] = useState("monday");
  
  const handleContinue = (e) => {
    props.goToStep(4);
  };
  const handleBack = (e) => {
    e.preventDefault();
    props.goToStep(2);
  };
  return (
    <fieldset>
      <Tabs
        className="tabs-container mt-1"
        activeKey={key}
        onSelect={(k) => setKey(k)}
      >
        <Tab eventKey="monday" title="Monday">
          <DynamicMeal index={1} />
        </Tab>
        <Tab eventKey="tuesday" title="Tuesday">
          <DynamicMeal index={2} />
        </Tab>
        <Tab eventKey="wednesday" title="Wednesday">
          <DynamicMeal index={3} />
        </Tab>
        <Tab eventKey="thursday" title="Thursday">
          <DynamicMeal index={4} />
        </Tab>
        <Tab eventKey="friday" title="Friday">
          <DynamicMeal index={5} />
        </Tab>
        <Tab eventKey="saturday" title="Saturday">
          <DynamicMeal index={6} />
        </Tab>
        <Tab eventKey="sunday" title="Sunday">
          <DynamicMeal index={7} />
        </Tab>
      </Tabs>

      <div className="col-lg-12 justify-content-end">
        <button
          type="button"
          className="btn btn-primary float-right mr-2 "
          onClick={handleContinue}
        >
          Next
        </button>
        <button
          type="button"
          className="btn btn-default float-right mr-2 "
          onClick={handleBack}
        >
          Previous
        </button>
      </div>
    </fieldset>
  );
}
